body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-image: url(/click-click/static/media/clouds.be3d3993.jpg);
  background-size: cover;
  font-family: 'Oswald', sans-serif;
}

.card-style {
  max-width: 200px;
  max-height: 200px;
  margin: 30px;
  border-radius:20px; 
  padding:10px;
}

.card-img {
  width: 150px;
  cursor: pointer;
}


.image-center {
  margin: auto;
}

.jumbotron{
  padding: 5px;
  border-radius: 200px;
  background-color: rgb(255, 254, 254, 0.3)
}

.navigation-center {
  text-align: center;
  margin: auto;
  display: flex;
}

@media only screen and (max-width: 600px) {
  body {
    background: url("https://i.pinimg.com/originals/06/ac/14/06ac1440d0c8aec99691ff58af62dd33.png") no-repeat;


  }
  .jumbotron{
    text-align: center;
  }

  .card-style {
    max-width: 50px;
    max-height: 50px;
    margin: 20px;
    border-radius:10px; 
    padding:2px;
  }
  .card-img {
    width: 50px;
  }
}
